import React, { useEffect, useState } from 'react';
import axios from 'axios';

function VisitorLog() {
  const [visits, setVisits] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [status, setStatus] = useState('');
  const [filter, setFilter] = useState('');
  const [search, setSearch] = useState('');

  const fetchVisits = async () => {
    setLoading(true);
    setError('');
    try {
      const params = {};
      if (status) params.status = status;
      if (filter) params.filter = filter;
      if (search) params.search = search;
      const res = await axios.get('/api/visits', { params });
      setVisits(res.data);
    } catch (err) {
      setError('Failed to fetch visitor log');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchVisits();
    // eslint-disable-next-line
  }, [status, filter, search]);

  // CSV export helper
  const exportToCSV = () => {
    if (!visits.length) return;
    const headers = [
      'Guest Name', 'Phone', 'Flat', 'Status', 'Purpose', 'Expected Arrival', 'Checked In At', 'Created At'
    ];
    const rows = visits.map(visit => [
      visit.name,
      visit.phone,
      visit.flat_number,
      visit.status,
      visit.purpose || '-',
      visit.expected_arrival ? new Date(visit.expected_arrival).toLocaleString() : '-',
      visit.checked_in_at ? new Date(visit.checked_in_at).toLocaleString() : '-',
      visit.created_at ? new Date(visit.created_at).toLocaleString() : '-'
    ]);
    const csvContent = [
      headers.join(','),
      ...rows.map(row => row.map(field => `"${String(field).replace(/"/g, '""')}"`).join(','))
    ].join('\n');
    const blob = new Blob([csvContent], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'visitor_log.csv';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="max-w-6xl mx-auto py-8">
      <h2 className="text-2xl font-bold mb-6">Visitor Log</h2>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      <div className="flex flex-wrap gap-4 mb-4">
        <select value={status} onChange={e => setStatus(e.target.value)} className="border p-2 rounded">
          <option value="">All Statuses</option>
          <option value="pending">Pending</option>
          <option value="checked_in">Checked In</option>
          <option value="rejected">Rejected</option>
        </select>
        <select value={filter} onChange={e => setFilter(e.target.value)} className="border p-2 rounded">
          <option value="">All Dates</option>
          <option value="today">Today</option>
          <option value="week">This Week</option>
          <option value="month">This Month</option>
        </select>
        <input
          type="text"
          value={search}
          onChange={e => setSearch(e.target.value)}
          placeholder="Search guest name or phone"
          className="border p-2 rounded w-64"
        />
        <button onClick={fetchVisits} className="px-4 py-2 bg-indigo-600 text-white rounded hover:bg-indigo-700">Refresh</button>
        <button onClick={exportToCSV} className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700">Export to CSV</button>
      </div>
      {loading ? (
        <div>Loading...</div>
      ) : visits.length === 0 ? (
        <div className="text-gray-500">No visitor records found</div>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Guest Name</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Phone</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Flat</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Purpose</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Expected Arrival</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Checked In At</th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Created At</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {visits.map((visit) => (
                <tr key={visit.id}>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.name}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.phone}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.flat_number}</td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                      visit.status === 'pending'
                        ? 'bg-yellow-100 text-yellow-800'
                        : visit.status === 'checked_in'
                        ? 'bg-green-100 text-green-800'
                        : visit.status === 'rejected'
                        ? 'bg-red-100 text-red-800'
                        : 'bg-gray-100 text-gray-800'
                    }`}>
                      {visit.status.replace('_', ' ')}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.purpose || '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.expected_arrival ? new Date(visit.expected_arrival).toLocaleString() : '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.checked_in_at ? new Date(visit.checked_in_at).toLocaleString() : '-'}</td>
                  <td className="px-6 py-4 whitespace-nowrap">{visit.created_at ? new Date(visit.created_at).toLocaleString() : '-'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default VisitorLog; 