import React, { useEffect, useState } from 'react';
import axios from 'axios';

function SecurityGuards() {
  const [guards, setGuards] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [processingId, setProcessingId] = useState(null);
  const [formData, setFormData] = useState({ name: '', phone: '', password: '' });
  const [editingId, setEditingId] = useState(null);

  const fetchGuards = async () => {
    setLoading(true);
    setError('');
    try {
      const res = await axios.get('/api/admin/security-guards');
      setGuards(res.data);
    } catch (err) {
      setError('Failed to fetch security guards');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchGuards();
  }, []);

  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(''); setSuccess('');
    try {
      if (editingId) {
        await axios.put(`/api/admin/security-guards/${editingId}`, formData);
        setSuccess('Security guard updated');
      } else {
        await axios.post('/api/admin/security-guards', formData);
        setSuccess('Security guard added');
      }
      setFormData({ name: '', phone: '', password: '' });
      setEditingId(null);
      fetchGuards();
    } catch (err) {
      setError(err.response?.data?.error || 'Failed to save security guard');
    }
  };

  const handleEdit = (guard) => {
    setFormData({ name: guard.name, phone: guard.phone, password: '' });
    setEditingId(guard.id);
  };

  const handleDelete = async (id) => {
    if (!window.confirm('Delete this security guard?')) return;
    setProcessingId(id);
    setError(''); setSuccess('');
    try {
      await axios.delete(`/api/admin/security-guards/${id}`);
      setSuccess('Security guard deleted');
      fetchGuards();
    } catch (err) {
      setError('Failed to delete security guard');
    } finally {
      setProcessingId(null);
    }
  };

  return (
    <div className="max-w-3xl mx-auto py-8">
      <h2 className="text-2xl font-bold mb-6">Security Guard Management</h2>
      {error && <div className="text-red-600 mb-2">{error}</div>}
      {success && <div className="text-green-600 mb-2">{success}</div>}
      <form onSubmit={handleSubmit} className="mb-6">
        <div className="flex gap-2">
          <input
            type="text"
            name="name"
            placeholder="Name"
            value={formData.name}
            onChange={handleInputChange}
            className="border p-2 rounded"
            required
          />
          <input
            type="text"
            name="phone"
            placeholder="Phone"
            value={formData.phone}
            onChange={handleInputChange}
            className="border p-2 rounded"
            required
          />
          <input
            type="password"
            name="password"
            placeholder="Password"
            value={formData.password}
            onChange={handleInputChange}
            className="border p-2 rounded"
            required={!editingId}
          />
          <button type="submit" className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700">
            {editingId ? 'Update' : 'Add'} Guard
          </button>
        </div>
      </form>
      {loading ? (
        <div>Loading...</div>
      ) : guards.length === 0 ? (
        <div className="text-gray-500">No security guards found</div>
      ) : (
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Name</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Phone</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {guards.map((guard) => (
              <tr key={guard.id}>
                <td className="px-6 py-4 whitespace-nowrap">{guard.name}</td>
                <td className="px-6 py-4 whitespace-nowrap">{guard.phone}</td>
                <td className="px-6 py-4 whitespace-nowrap flex gap-2">
                  <button
                    onClick={() => handleEdit(guard)}
                    className="px-3 py-1 rounded bg-yellow-600 text-white hover:bg-yellow-700"
                  >
                    Edit
                  </button>
                  <button
                    onClick={() => handleDelete(guard.id)}
                    disabled={processingId === guard.id}
                    className="px-3 py-1 rounded bg-red-600 text-white hover:bg-red-700 disabled:opacity-50"
                  >
                    {processingId === guard.id ? 'Deleting...' : 'Delete'}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default SecurityGuards; 