import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import axios from 'axios';

const AdminPanel = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [notifStatus, setNotifStatus] = React.useState('');
  const [notifError, setNotifError] = React.useState('');

  const sendTestNotification = async () => {
    setNotifStatus('Sending...');
    setNotifError('');
    try {
      const response = await axios.post('/api/notifications/test', {
        userId: user?.id,
        title: 'Test Notification',
        body: 'This is a test notification from the Admin Panel.'
      });
      setNotifStatus('Notification sent!');
      console.log('[Test Notification] Success:', response.data);
    } catch (error) {
      setNotifStatus('Failed to send notification');
      setNotifError(
        JSON.stringify({
          message: error.message,
          response: error.response?.data,
          stack: error.stack
        }, null, 2)
      );
      console.error('[Test Notification] Error:', error);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-7xl mx-auto">
        <div className="text-center">
          <h2 className="text-3xl font-extrabold text-gray-900">
            Admin Dashboard
          </h2>
          <p className="mt-2 text-sm text-gray-600">
            Manage your gated community
          </p>
        </div>

        <div className="mt-8 grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="px-4 py-5 sm:p-6">
              <h3 className="text-lg font-medium text-gray-900">Pending Users</h3>
              <p className="mt-1 text-sm text-gray-500">
                Review and approve new user registrations
              </p>
              <div className="mt-4">
                <button
                  onClick={() => navigate('/admin/pending-users')}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  View Pending Users
                </button>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="px-4 py-5 sm:p-6">
              <h3 className="text-lg font-medium text-gray-900">Apartment Management</h3>
              <p className="mt-1 text-sm text-gray-500">
                Create, edit, or remove blocks and flats
              </p>
              <div className="mt-4">
                <button
                  onClick={() => navigate('/admin/apartment-management')}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  Apartment Management
                </button>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="px-4 py-5 sm:p-6">
              <h3 className="text-lg font-medium text-gray-900">Security Guards</h3>
              <p className="mt-1 text-sm text-gray-500">
                Manage security guard accounts
              </p>
              <div className="mt-4">
                <button
                  onClick={() => navigate('/admin/security-guards')}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  Security Guards
                </button>
              </div>
            </div>
          </div>

          <div className="bg-white overflow-hidden shadow rounded-lg">
            <div className="px-4 py-5 sm:p-6">
              <h3 className="text-lg font-medium text-gray-900">Visitor Log</h3>
              <p className="mt-1 text-sm text-gray-500">
                View and manage visitor check-ins
              </p>
              <div className="mt-4">
                <button
                  onClick={() => navigate('/visitor-log')}
                  className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                >
                  View Visitor Log
                </button>
              </div>
            </div>
          </div>
        </div>

        <div className="mt-6">
          <button
            onClick={sendTestNotification}
            className="w-full flex justify-center py-2 px-4 border border-indigo-600 rounded-md shadow-sm text-sm font-medium text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 mb-2"
          >
            Send Test Notification
          </button>
          {notifStatus && <div className="text-center text-sm mt-2">{notifStatus}</div>}
          {notifError && <pre className="text-xs text-red-600 bg-red-50 rounded p-2 mt-2 overflow-x-auto">{notifError}</pre>}
          <button
            onClick={() => navigate('/dashboard')}
            className="w-full flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
          >
            Back to Dashboard
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminPanel; 